import Vue from 'vue'

//表格数据转换，将 SOCIAL 和 PRIVATE 转换为社会和私有
Vue.filter('transformType' , function(value) {
    return value === "SOCIAL" ? "社会" : "自有"
})

// //表格数据转换，将获取到店铺状态的数据进行转换
Vue.filter('transformStatus' , function(value) {
    return value = value === "OPEN" ? "开店" : ( value === "CLOSE" ? "关店" : "歇业")
})


