<template>
  <el-card>
    <el-row :gutter="10">
      <el-col :span="5">
        <el-input placeholder="请输入店铺名称" v-model="input" autofocus="true" clearable></el-input>
      </el-col>
      <el-col :span="2">
        <el-button type="primary" @click="search" icon="el-icon-search">搜索</el-button>
      </el-col>
      <el-col :span="5" :offset="12">
        <el-button type="primary" @click="newShopClick" icon="el-icon-circle-plus-outline">新建店铺</el-button>
      </el-col>
    </el-row>
    <el-row class="mtb10">
      <el-table :data="tableData"  border stripe class="w24">
        <el-table-column prop="branchCompanyName" label="分公司" align="center"></el-table-column>
        <el-table-column prop="shopName" label="店铺名称" align="center"></el-table-column>
        <el-table-column prop="shopType" label="店铺类型" width="80" align="center">
          <template slot-scope="scope">
            {{scope.row.shopType | transformType}}
          </template>
        </el-table-column>
        <el-table-column prop="userName" label="店长" align="center"></el-table-column>
        <el-table-column prop="shopStatus" label="店铺状态" align="center" width="300">
          <template slot-scope="scope">
            {{scope.row.shopStatus | transformStatus}}
            <el-tooltip content="开店" placement="top">
              <el-button type="success" :disabled="scope.row.shopStatus === 'OPEN'" class="iconfont icon-kaidian" size="mini" @click="confirmClick(scope.$index, scope.row, 'OPEN')"></el-button>
            </el-tooltip>
            <el-tooltip content="歇业" placement="top">
              <el-button type="warning" :disabled="scope.row.shopStatus === 'LAJI'" class="iconfont icon-xieyezhong" size="mini" @click="confirmClick(scope.$index, scope.row, 'REST')"></el-button>
            </el-tooltip>
            <el-tooltip content="关店" placement="top">
              <el-button type="danger" :disabled="scope.row.shopStatus === 'CLOSE'" class="iconfont icon-guandian" size="mini" @click="confirmClick(scope.$index, scope.row, 'CLOSED')"></el-button>
            </el-tooltip>
            
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200" align="center">
          <template slot-scope="scope">
            <el-tooltip content="编辑店铺" placement="top">
              <el-button
              type="primary"
              size="mini"
              icon="el-icon-edit"
              @click="operateClick(scope.$index, scope.row, 'editShop')"
            ></el-button>
            </el-tooltip>
            <el-tooltip content="更改店长" placement="top">
              <el-button
              type="primary"
              size="mini"
              icon="el-icon-edit-outline"
              @click="operateClick(scope.$index, scope.row, 'changeManager')"
            ></el-button>
            </el-tooltip>  
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row class="df jcfe">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current_page="current_page"
        :page-sizes="[2, 4, 6]"
        :page_size="page_size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      ></el-pagination>
    </el-row>
  </el-card>
</template>

<script>
import '../filters'
export default {
  data() {
    return {
      //分页
      current_page: 2,
      page_size: 2,
      total: 0,
      input: "",
      //表格数据
      tableData: []
    };
  },
  methods: {
    //搜索
    async search(){
      let res = await this.$get('/shop/shopData/getShopAll',{
        branchCompanyDictId: 1,
        pageSize: this.page_size,
        pageNum: this.current_page,
        shopName: this.input
      })
      this.total = res.total
      let data = res.shopResponseList
      // data.map( (item) => {
      //   item.shopStatus = item.shopStatus === "CLOSED" ? "关店" : (item.shopStatus === "OPEN" ? "开店" : "歇业")
      //   item.shopType = item.shopType === "SOCIAL" ? "社会" : "自有"
      // })
      this.tableData = data
    },
    //开店歇业关店操作
    confirmClick(index , row , shopStatus) {
      this.$confirm(`您确定执行${shopStatus === 'OPEN' ? '开店' : (shopStatus === 'CLOSED' ? '关店' : '歇业')}操作吗?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$put('shop/shopData/updateShopState',{
            shopId: row.shopId,
            shopState: shopStatus
          },{
            "Content-Type":"application/json"
          })
          this.$message({
            type: "success",
            message: "操作成功!"
          });
        })
        .then(() => {
          this.$router.push("/home/shop/shopList");
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消该操作"
          });
        });
    },
    
    //新建店铺
    newShopClick() {
      this.$router.push("/page/shop/newShop");
    },
    // //编辑店铺
    // editShopClick(index, row) {
    //   this.$router.push({
    //     name: "editShop",
    //     query: { shopId: row.shopId }
    //   });
    //   console.log(index, row);
    // },
    // //更改店长
    // changeManagerClick(index, row) {
    //   this.$router.push({
    //     name: "changeManager",
    //     query: { shopId: row.shopId }
    //   });
    // },

    //编辑店铺和更改店长
    operateClick(index, row, operateName) {
      this.$router.push({
        name: operateName,
        query: { shopId: row.shopId }
      });
      console.log(index, row);
    },
    //更改每页的数量
    async handleSizeChange(val) {
      console.log(val)
      this.page_size = val
      let res = await this.$get('/shop/shopData/getShopAll',{
        pageSize: val,
        pageNum: this.current_page,
        branchCompanyDictId: 1,
        shopName: this.input
      })
      let data = res.shopResponseList
      // data.map( (item) => {
      //   item.shopStatus = item.shopStatus === "CLOSED" ? "关店" : (item.shopStatus === "OPEN" ? "开店" : "歇业")
      //   item.shopType = item.shopType === "SOCIAL" ? "社会" : "自有"
      // })
      this.tableData = data
    },
    //前往多少页，改变当前页
    async handleCurrentChange(val) {
      console.log(val + '当前多少页');
      this.current_page = val
      let res = await this.$get('/shop/shopData/getShopAll',{
        pageSize: this.page_size,
        pageNum: val,
        branchCompanyDictId: 1,
        shopName: this.input
      })
      console.log(res);
      let data = res.shopResponseList
      // data.map( (item) => {
      //   item.shopStatus = item.shopStatus === "CLOSED" ? "关店" : (item.shopStatus === "OPEN" ? "开店" : "歇业")
      //   item.shopType = item.shopType === "SOCIAL" ? "社会" : "自有"
      // })
      this.tableData = data
    },

    async getShopListData() {
      //请求本地json数据
      let data = await this.$get('../../../../static/shopList.json',{
        branchCompanyDictId: 1
      })
      console.log(data);
      this.tableData = data
      this.total = this.tableData.length
    }
  },
  async mounted() {
    this.getShopListData()
    //请求获取店铺的数据
    // let res = await this.$get('/shop/shopData/getShopAll',{
    //   pageSize: this.page_size,
    //   pageNum: this.current_page,
    //   branchCompanyDictId: 1
    // })
    // this.total = res.total
    // let data = res.shopResponseList
    // data.map( (item) => {
    //   item.shopStatus = item.shopStatus === "CLOSED" ? "关店" : (item.shopStatus === "OPEN" ? "开店" : "歇业")
    //   item.shopType = item.shopType === "SOCIAL" ? "社会" : "自有"
    // })
    // this.tableData = data
    // console.log(this.tableData);
    

    //请求本地json数据
    // let data = await this.$get('../../../../static/shopList.json',{
    //   branchCompanyDictId: 1
    // })
    // console.log(data);
    // data.map( (item) => {
    //   item.shopStatus = item.shopStatus === "CLOSE" ? "关店" : (item.shopStatus === "OPEN" ? "开店" : "歇业")
    //   item.shopType = item.shopType === "SOCIAL" ? "社会" : "自有"
    // })
    
    
    // this.tableData = data
    // this.total = this.tableData.length
    // console.log(this.tableData.length);
    

  }
};
</script>



